
























































































































































































































































import { Component, Vue } from 'vue-property-decorator';

@Component
export default class App extends Vue {
  created(): void {
    const secondTheme = document.createElement('div');
    secondTheme.className = 'second-theme';
    document.body.appendChild(secondTheme);
  }

  openMenu(): void {
    (this.$el.querySelector('#site-menu') as HTMLElement).classList.add(
      'menu-opened'
    );
    (document.querySelector('#site-nav-btn') as HTMLElement).classList.add(
      'nav-btn-hidden'
    );
  }

  closeMenu(): void {
    (this.$el.querySelector('#site-menu') as HTMLElement).classList.remove(
      'menu-opened'
    );
    (document.querySelector('#site-nav-btn') as HTMLElement).classList.remove(
      'nav-btn-hidden'
    );
  }

  toggleTheme(): void {
    (document.querySelector('.second-theme') as HTMLElement).classList.toggle(
      'second-theme-shown'
    );
  }
}
